export default function RapportInvoice() {
  const { themeStretch } = useSettings();
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { user } = useAuth();

  const { facturations } = useSelector((state) => state.user);
  const { factuId } = useParams();
  const invoice = facturations?.find((faac) => faac?.id === Number(factuId));

  const handleDate = (dateString) => {
    const date = new Date(dateString);
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const day = date.getDate();
    const formattedDate = `${year}-${month}-${day}`;
    return formattedDate;
  };

  // useEffect(() => {
  //   dispatch(getFacturations(handleDate(new Date()), handleDate(new Date()), 0));
  // }, []);

  const randomDigits = Array.from({ length: 7 }, () => random(9)).join('');

  const handleInvoice = () => {
    navigate(PATH_DASHBOARD.company.companyFactu);
  };

  return (
    <Page title="Facturation: Details | RLP Center Lotto">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Facturation Details"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            {
              name: 'Facturation',
              href: PATH_DASHBOARD.company.companyFactu
            },
            { name: 'Details' }
          ]}
        />

        <InvoiceToolbar invoice={invoice} randomDigits={randomDigits} handleInvoice={handleInvoice} />
        <Card sx={{ pt: 5, px: 5 }}>
          <ExtraComp invoice={invoice} randomDigits={randomDigits} />
        </Card>
      </Container>
    </Page>
  );
}

